
<template>
  <div id="page-content" class="header-static">
    <div class="container">
      <form id="contact-form">
        <div class="submit-area padding-sm">
          <div class="row no-margin padding-onlytop-sm">
            <div class="inline-block margin-lr-lg">
              <div class="padding-onlybottom-sm bold">每100克热量</div>
              <div>
                <input @focus="focus('kk')" type="number" v-model="kk" /> 大卡(千卡)
              </div>
              <div>
                <input @focus="focus('kj')" type="number" v-model="kj" /> 千焦
              </div>
            </div>
            <div class="inline-block margin-lr-lg">
              <div class="padding-onlybottom-sm bold">重量</div>
              <div>
                <input @focus="focus('g')" type="number" v-model="g" /> 克
              </div>
              <div>
                <input @focus="focus('halfKg')" type="number" v-model="halfKg" /> 斤
              </div>
            </div>
            <div class="inline-block margin-lr-lg">
              <div class="padding-onlybottom-sm bold">总热量</div>
              <div class="input-height">{{ (kj*g *0.01).toFixed(2) }} 千焦</div>
              <div class="input-height">{{ (kk*g *0.01).toFixed(2) }} 大卡(千卡)</div>
            </div>
          </div>
          <div class="row no-margin padding-onlytop-sm">
            <div class="inline-block margin-lr-lg">
              <div class="padding-onlybottom-sm bold">相当于</div>
              <div
                class="inline-block input-height margin-lr-lg"
                v-bind:key="index"
                v-for="(item,index) in calorieData"
              >{{(kk*g *0.01/item.kCalPerUnit).toFixed(2)+item.unit+item.name}}</div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
 
<script>
export default {
  data: function () {
    return {
      currentType: '',
      kj: 0,
      kk: 0,
      g: 0,
      halfKg: 0,
      calorieData: [
        { unit: '碗', name: '白米饭', kCalPerUnit: 209 },
        { unit: '个', name: '馒头', kCalPerUnit: 268 },
        { unit: '份', name: '糙米饭', kCalPerUnit: 174 },
        { unit: '根', name: '黄瓜', kCalPerUnit: 29 },
        { unit: '小碗', name: '小米粥', kCalPerUnit: 92 },
        { unit: '个', name: '麦辣鸡腿堡', kCalPerUnit: 485 },
        { unit: '只', name: '鸡蛋', kCalPerUnit: 73 },
        { unit: '杯', name: '牛奶', kCalPerUnit: 132 },
        { unit: '根', name: '玉米', kCalPerUnit: 111 },
        { unit: '个', name: '番茄', kCalPerUnit: 25 },
        { unit: '罐', name: '啤酒', kCalPerUnit: 142 },
      ],
    }
  },

  watch: {
    kj: function () {
      //两个变量互相监听会造成死循环，所以这里判断一下没必要更新就不更新
      if (this.currentType == 'kj') {
        let targetVal = this.kj / 4.1868
        this.kk = parseFloat(targetVal.toFixed(3))
      }
    },
    kk: function () {
      if (this.currentType == 'kk') {
        let targetVal = this.kk * 4.1868
        this.kj = parseFloat(targetVal.toFixed(3))
      }
    },
    g: function () {
      if (this.currentType == 'g') {
        let targetVal = this.g * 0.002
        this.halfKg = parseFloat(targetVal.toFixed(3))
      }
    },
    halfKg: function () {
      if (this.currentType == 'halfKg') {
        let targetVal = this.halfKg * 500
        this.g = parseFloat(targetVal.toFixed(3))
      }
    },
  },
  computed: {},
  methods: {
    focus: function (type) {
      this.currentType = type
    },
  },
}
</script>
<style scoped>
.container {
  width: 80%;
  margin: auto;
}
.input-height {
  height: 64px;
}
</style>

